.row {
  width: 95vw;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin-top: 2rem;
}
.title {
  display: flex;
  flex: 0 1 50%;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 34px;
}
