@import "./../colors.scss";
@import "./../variables.scss";

.container {
  position: relative;
  border: 1px solid black;
  width: 33%;
  & img {
    position: relative;
    width: 100%;
    display: block;
  }
  &:hover .btnClose {
    opacity: 1;
    right: -10px;
  }

  .btnClose {
    font-size: 1.2rem;
    color: white;
    position: absolute;
    top: -10px;
    right: -15px;
    cursor: pointer;
    background-color: black;
    border-radius: $custom-radius;

    padding: 8px 16px;
    opacity: 0;
    transition: all 0.1s ease-in;
  }
}
