@import "./colors.scss";
@import "./variables.scss";

.App {
  &-header {
    // min-height: 20vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 1.4rem;
    padding: 1.6rem 2rem;
    background-color: rgba($blue-3, 1);
    color: rgba($white-1, 1);
  }
  &-tools {
    padding: 1rem;
    font-size: 0.6rem;
    background-color: $grey-1;
  }
}

.currentSectionIndicator {
  font-size: 12px;
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

// DESIGN
.hidden {
  display: none !important;
}

// LOADING
.skeleton {
  margin-top: 15px;
  &-bloc {
    width: 100%;
    height: 44px;
    // background-color: #bbb;
    margin-bottom: 5px;
    background-color: rgb(243, 243, 243);
    border-radius: 1px;
    background-image: linear-gradient(90deg, transparent, rgb(250, 250, 250), transparent);
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: -200% 0;
    animation: anim 1.1s infinite ease-in-out;
    border: 1px solid rgba(255, 255, 255, 0.2);
    &-offset {
      // width: 75%;
      animation: anim 1.1s infinite ease-in-out 0.05s;
    }
  }
}
@keyframes anim {
  to {
    background-position: 200% 0;
  }
}

// Debug
.tag {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: $lightgrey-3;
  border-radius: $custom-radius;

  color: $white-1;
  font-weight: bold;
  font-size: 0.6rem;
  padding: 0.25rem;
  width: 335px;
  cursor: pointer;
  margin-right: 0.5rem;
  // height: 100%;
  textarea {
    position: relative;
    display: block;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    margin-top: 5px;
    font-size: 0.8rem;
    background-color: #fff;
  }
}
.debug-toggle {
  font-size: 0.8rem;
  display: flex;
  align-items: center;
}

// ---- Common ----------- //
h1 {
  font-size: 1.2rem;
  margin: 0;
}
h2 {
  font-size: 0.9rem;
  margin: 0;
}
h3 {
  font-size: 0.8rem;
  font-weight: normal;
  margin: 0 0 6px 6px;
}
.textcut {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  &-lg {
    width: 90%;
  }
}
textarea {
  border: 0;
  outline: none;
  transition: all 0.25s;
  &:focus {
    border: 1px solid $grey-3;
  }
}
select {
  outline: none;
}
input[type="text"] {
  border: none;
  box-sizing: border-box;
  border-bottom: 1px solid $grey-1;
  border-radius: 0;
  width: 100%;
  outline: none;
  transition: all 0.25s;
  min-height: 38px;
  padding: 0 12px;
  &:focus {
    border-color: $grey-3;
  }
}
input[type="checkbox"] {
  cursor: pointer;
  appearance: none;
  outline: none;
  width: 14px;
  background-color: white;
  border: 1px solid $grey-1;
  padding: 7px;
  border-radius: 2px;
  display: inline-block;
  position: relative;
  &:checked {
    background-color: $primary-1;
    border: 1px solid $primary-1;
  }
  &:checked:after {
    content: "\2714";
    font-size: 13px;
    position: absolute;
    top: -1px;
    left: 2px;
    background-color: $primary-1;
    color: white;
  }
}
input[type="radio"] {
  cursor: pointer;
  appearance: none;
  outline: none;
  border-radius: 50%;
  // width: 18px;
  background-color: white;
  border: 1px solid $grey-1;
  min-width: unset;
  padding: 7px;
  margin-left: 10px;
  display: inline-block;
  position: relative;
  &:checked {
    background-color: $primary-1;
    border: 1px solid $primary-1;
  }
  &:checked:after {
    content: " ";
    width: 8px;
    height: 8px;
    border-radius: 50%;
    position: absolute;
    top: 3px;
    left: 3px;
    background-color: white;
    text-shadow: none;
    font-size: 32px;
  }
}
.radiocheck {
  &-button {
    margin-left: 6px;
    margin-right: 12px;
    padding: 2px 8px;
    border-radius: 2px;
  }
}
// ---- Common End ------- //
.board {
  width: 95vw;
  max-width: 800px;
  margin: 0 auto;
  min-height: 500px;
  background-color: $blue-2;
  box-sizing: border-box;
  padding: 3rem;
  margin-bottom: 17rem;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: $custom-radius;

  box-shadow: 0 0.46875rem 2.1875rem rgba(0, 0, 0, 0.03), 0 0.9375rem 1.40625rem rgba(0, 0, 0, 0.03), 0 0.25rem 0.53125rem rgba(0, 0, 0, 0.05), 0 0.125rem 0.1875rem rgba(0, 0, 0, 0.03);
  &-debug {
    max-width: unset;
    padding: 0.4rem;
    & [class*="matrix"] {
      & .matrix-box .control {
        max-width: 220px;
      }
      & .tag {
        max-width: 140px;
      }
    }
  }
}
.toc-list {
  list-style: none;
  padding: 0;
  & li {
    display: flex;
    justify-content: center;
    // width: 300px;
    padding: 0;
  }
}
.control {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // margin-left: 0.5rem;
  width: 100%;
  &-id {
    color: rgba($grey-2, 0.7);
  }
  &-container {
    display: flex;
    // padding: 1.8rem 0 3.4rem;
    padding: 0.8rem 1rem 1.4rem;
    width: 100%;
    box-sizing: border-box;
    &-matrix {
      padding: 0 0 2rem;
    }
  }
  &-row {
    padding: 1rem 0;
  }
  & label {
    margin: 0 0 6px 6px;
  }
  &-checkbox {
    display: flex;
    flex-direction: row;
    // justify-content: space-between;
    align-items: center;
    // padding: 4px;
    &-option {
      margin: 16px 0 0px 0;
    }
    & label {
      margin: 0 0 0 16px;
      // margin-bottom: 0;
      //   margin: 0 12px 0 0;
    }
  }
  &-details {
    &-container {
      display: flex;
      width: 100%;
      padding: 0.6rem 0.4rem 0.4rem 0.4rem;
    }
    &-toggle {
      cursor: pointer;
      padding: 0 0.7rem 0 0;
    }
  }
  &-inline {
    margin-top: 10px;
    display: flex;
    & input:not([type="checkbox"]) &:not([type="radio"]),
    select {
      min-width: 160px;
    }
  }
  &-underline {
    padding-bottom: 10px;
    border-bottom: 1px solid $grey-2;
  }
}
// SUMMARY
.summary {
  &-details {
    &-row {
      display: flex;
      justify-content: space-between;
      padding: 12px;
      &:nth-child(odd) {
        background-color: $lightgrey-2;
      }
      & h5 {
        margin: 0;
      }
    }
  }
}

// UI
.btn {
  border-radius: $custom-radius;

  border: 0;
  padding: 6px 12px;
  cursor: pointer;
  &:focus,
  &:active {
    outline: none;
  }
  &:disabled {
    background-color: rgba($grey-1, 0.5);
    color: $grey-1;
    cursor: default;
    &:hover {
      color: $grey-1;
      filter: inherit;
    }
  }
  &:hover {
    color: $white-1;
    filter: contrast(120%);
  }
  &-primary {
    color: rgba($white-1, 0.9);
    background-color: rgba($primary-1, 1);
  }
  &-secondary {
    color: rgba($white-1, 1);
    background-color: rgba($grey-2, 1);
    &-outlined {
      color: rgba($grey-2, 1);
      background-color: rgba($white-1, 0.3);
      border: 0;
      border-bottom: 1px solid rgba($grey-2, 0.2);
      &:hover {
        color: rgba($black-1, 0.7);
        background-color: rgba($white-1, 0.8);
        border-bottom: 1px solid rgba($grey-2, 0.5);
      }
    }
  }
  &-ternary {
    color: rgba($white-1, 1);
    background-color: rgba($blue-1, 1);
  }
  &-link {
    display: flex;
    justify-content: space-between;
    &:focus,
    &:active {
      outline: none;
    }
  }
  &-file {
    opacity: 0;
    position: absolute;
    z-index: -1;
  }
  &-small {
    padding: 0.4rem 0.9rem;
    font-size: 0.7rem;
  }
  &-label {
    & svg {
      margin-right: 10px;
    }
  }
  &-icon {
    display: inline-flex;
    align-items: center;
    & svg {
      margin-right: 10px;
    }
  }
}
.fixed {
  &-medium {
    cursor: pointer;
    width: 400px;
    padding: 0.8rem 2.3rem;
    & svg {
      transition: all 0.1s ease-in;
      position: relative;
      left: 0px;
    }
    &:hover svg {
      left: 6px;
    }
  }
  &-large {
    cursor: pointer;
    width: 90%;
    padding: 1.4rem 2.1rem 0.7rem;
    font-weight: bold;
    & svg {
      transition: all 0.1s ease-in;
      position: relative;
      left: 0px;
    }
    &:hover svg {
      left: 6px;
    }
  }
}

// INPUTS
.input {
  &-select {
    padding: 8px 0 6px 4px;
    border-bottom: 1px solid $grey-2;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    font-size: 0.7rem;
    background-color: $lightgrey-2;
    &:hover {
      background-color: $lightgrey-3;
    }
    &-items {
      display: flex;
      width: 100%;
      user-select: none;
      // height: 28px;
    }
    &-item {
      position: relative;
      display: flex;
      align-items: center;
      background-color: $blue-1;
      margin: 0 6px;
      border-radius: $custom-radius;

      color: $white-1;
      padding: 1px 2px 1px 10px;
      cursor: default;
      font-weight: bold;
      &:hover {
        // background-color: $grey-2;
        // text-decoration: line-through $white-1;
        // color: $lightgrey-3;
        &:after {
          // content: ' X'
        }
      }
    }
    & svg {
      padding: 6px 16px 6px 6px;
    }
    & .remove-item {
      border: 0;
      border-radius: $custom-radius;

      cursor: pointer;
      background-color: $whitetransparent-1;
      color: $whitetransparent-3;
      outline: none;
      padding-bottom: 2px;
      padding: 0 6px 2px 6px;
      margin: 0 1px 0 14px;
      &:hover {
        background-color: $white-1;
        color: $grey-2;
      }
      &:after {
        content: "x";
      }
    }
  }
  &-list {
    background-color: $white-1;
    overflow: hidden;
    &-item {
      padding: 4px 10px;
      margin: 2px 0;
      cursor: pointer;
      user-select: none;
      border-radius: $custom-radius;

      &:hover {
        background-color: $blue-1;
        color: $white-1;
      }
    }
    &-search {
      padding: 6px;
    }
    &-searchbar {
      padding: 1rem 0 0 2rem;
      width: 40%;
      min-width: 200px;
      & svg {
        position: relative;
        float: right;
        top: -22px;
        left: -15px;
        cursor: pointer;
        color: $grey-1;
      }
    }
    &-items {
      position: relative;
      top: -13px;
      width: 100%;
      height: 150px;
      padding: 1rem 2rem 1rem 2rem;
      overflow: hidden;
      overflow-y: scroll;
      box-sizing: border-box;
    }
  }
}
.selected {
  &-list {
    &-item {
      background-color: $blue-1;
      color: $white-1;
      &:hover {
        background-color: $grey-2;
        text-decoration: line-through;
      }
      & svg {
        margin-right: 6px;
      }
    }
  }
}

// COMPONENTS
.matrix {
  &-box {
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: rgb(241, 241, 241);
    padding: 6px 2px;
    // margin: 4px;
  }
  &-button {
    cursor: pointer;
    transition: all 0.1s ease-in;
  }
  &-container {
    display: grid;
    grid-template-columns: 40px 1fr;
  }
  &-yaxis {
    writing-mode: vertical-lr;
    // text-orientation: sideways;
    transform: scale(-1);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &-xaxis {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    justify-content: center;
  }
  &-legend {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    & div:nth-child(1) {
      margin: 0 44px;
    }
    & div:nth-child(2) {
      margin: 0 3px;
    }
    & h5 {
      padding-bottom: 5px;
      margin-bottom: 15px;
      border-bottom: 1px solid $grey-1;
      min-width: 200px;
    }
    & li {
      list-style-type: none;
    }
  }
}
.repeatable {
  margin-bottom: 48px;
  &-header {
    display: flex;
    padding: 0 0 4px 0;
    margin-bottom: 18px;
    // flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid $grey-3;
    & .btn svg {
      margin-left: 12px;
    }
  }
  &-title {
    display: inline-block;
    margin: 0;
  }
}
.qrscan {
  &-record {
    display: flex;
    // justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $grey-1;
    padding: 8px 0;
    & div {
      flex: 0 1 30%;
      text-align: center;
    }
    & div:nth-child(1) {
      text-align: left;
    }
    & button {
      flex: 0 1 10%;
    }
    &-3cols {
      & div {
        flex: 0 1 45%;
        text-align: left;
      }
      & button {
        flex: 0 1 10%;
      }
    }
  }
}

.listview {
  &-record {
    display: flex;
    // justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $grey-1;
    padding: 8px 0;
    & div {
      flex: 0 1 30%;
      text-align: center;
    }
    & div:nth-child(1) {
      text-align: left;
    }
    & button {
      flex: 0 1 10%;
    }
  }
}

// SIGNATURES
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Herr+Von+Muellerhoff&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap");

#signaturePad {
  margin-top: 50px;
}
.signature {
  &-preview {
    // width: 200px;
    font-size: 2rem;
    padding: 5px 15px;
  }
  &-user {
    position: absolute;
    left: 0;
    bottom: 0;
    font-family: sans-serif;
    font-size: 0.7rem;
  }
}
.signature-container {
  position: absolute;
  top: -500px;
}
.signature-font01 {
  font-family: "Dancing Script", cursive;
}
.signature-font02 {
  font-family: "Architects Daughter", cursive;
}
.signature-font03 {
  font-family: "Herr Von Muellerhoff", cursive;
}
.signature-font04 {
  font-family: "Homemade Apple", cursive;
}
.register-signature-row {
  display: flex;
  border-bottom: 1px solid $grey-2;
  padding-bottom: 12px;
  margin-top: 32px;
  & img {
    margin-right: 32px;
  }
}
