@import "./../colors.scss";
@import "./../variables.scss";

$light-1: white;
$light-2: rgb(235, 235, 235);
$light-3: rgb(230, 230, 230);
$light-4: rgb(209, 209, 209);

$primary-1: #97d600;
$primary-2: #90cb05;
$secondary-1: #007db4;

$dark-1: black;
$dark-2: rgb(66, 66, 66);
$dark-3: rgb(129, 129, 129);

.selectItem {
  display: block;
  cursor: pointer;
  padding: 4px;
  &:hover {
    background-color: $primary-2;
  }
}
.selectedItem {
  &_delete {
    padding: 0px 4px 0px 4px;
    background-color: $light-4;
    border-radius: $custom-radius;

    margin-left: 9px;
    &:hover {
      background-color: $light-3;
      & svg {
        color: $dark-2;
      }
    }
    & svg {
      color: $dark-3;
    }
  }
}
.selectSearchable {
  margin-bottom: 32px;
  &_Container {
    position: relative;
    padding: 3px;
    background-color: $light-2;
    min-width: 150px;
  }
  &_SearchContainer {
    position: relative;
    width: 100%;
    margin-bottom: 6px;
    & input[type="text"] {
      position: relative;
      display: block;
      background-color: white;
      border-radius: 0;
      box-sizing: border-box;
      // width: 90%;
      border: none;
      border-bottom: 1px solid black;
      outline: none;
      padding: 10px;
      &:focus {
        outline: none;
      }
    }
    & svg {
      position: relative;
      float: right;
      top: -23px;
      left: -15px;
      cursor: pointer;
      color: black;
      z-index: 20;
    }
  }
  &_ItemsContainer {
    height: 200px;
    overflow: hidden;
    overflow-y: scroll;
    width: 100%;
    // background-color: $light-2;
  }
  &_Header {
    position: relative;
    display: flex;
    justify-content: space-between;
    min-height: 34px;
  }
  &_SelectedContainer {
    width: 100%;
    background-color: $light-1;
    padding: 3px;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
  }
  &_Trigger {
    background-color: $primary-1;
    color: white;
    width: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  &_SelectedItem {
    background-color: $dark-2;
    color: white;
    font-size: 0.8rem;
    display: inline-block;
    margin: 3px 2px 2px 0;
    padding: 4px 8px;
    border-radius: $custom-radius;

    &_Selected {
      margin: 2px 0;
      background-color: $primary-1;
      color: white;
    }
  }
  &_List {
    position: relative;
    display: block;
    left: 0;
    top: 37px;

    z-index: 10;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
      0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
    display: block;
    padding: 10px;
    background-color: $light-3;
    color: black;
  }
}
.listCollapsed {
  display: none;
  overflow-y: auto;
}
.ListSmall {
  width: 136px;
}
.ListMedium {
  width: 290px;
}
.ListLarge {
  width: 390px;
}
